/* Base */
@import "tailwindcss/base";
/* Components */
@import "tailwindcss/components";
/* Utilities */
@import "tailwindcss/utilities";

/* purgecss start ignore */
@import "reset";
@import "buttons";
@import "form";
@import "nprogress";

/* vue2-datepicker */
@import 'vue2-datepicker/index.css';

/* pretty checkbox*/
@import "pretty-checkbox/dist/pretty-checkbox.min.css"; 
.pretty input:not(:checked) .state label::after, .pretty .state label::before {
  @apply .border-4 .border-gray-600
}

@layer base {
  .page-header{
    @apply mb-4 text-3xl font-bold
  }
  .section-header{
    @apply mb-2 text-2xl font-bold
  }
}
@layer components {
  .mx-datepicker{
    width: 100% !important;
  }
}
/* purgecss end ignore */
